    "sup-title": "",
    "description": "‘ঘরর অভাব দূর গইত্তো যাই চ’রি লই লাশ অইয়ে আঁর পোয়া। ইতে আগামীবার দাখিল পরীক্ষা দিবার হথা। ঘরর অভাবও দূর নঅইল, পরীক্ষাও দিত নুপারিল আঁর পোয়া। ঘরর এদুগুন কর্জ লই আঁই হন্ডে যাইয়ুম, কী গইজ্জুম (সংসারের অভাব দূর করতে গিয়ে চাকরি নিয়ে লাশ হলো আমার ছেলে। আগামী বছর তার দাখিল পরীক্ষা দেওয়ার কথা। অভাব দূর হলো না, পরীক্ষাও দিতে পারল না আমার ছেলে। এত ঋণ নিয়ে আমি কোথায় যাব, কী করব)।’ এসব বলে বিলাপ করছিলেন চট্টগ্রামের বাঁশখালীর গন্ডামারায় সংঘর্ষের সময় পুলিশের গুলিতে নিহত মাহমুদ রেজা ওরফে মীর খানের (১৮) মা নূরাইন জান্নাত।",
